import {
    MenuItem,
    ListSeparator,
    LimelMenuCustomEvent,
} from '@limetech/lime-elements';
import { Component, h, State } from '@stencil/core';

/**
 * Menu with supporting hotkeys
 *
 * `commandText` is only visual.
 * Use `hotkey` to bind actual keyboard interaction while the menu is open.
 *
 * Some keys are reserved for menu interaction and will be ignored as hotkeys:
 * `enter`, `escape`, `space`, `tab`, and the arrow keys.
 * If you need one of those keys, use a modifier (e.g. `alt+enter`).
 *
 * :::note
 * All browsers have some built-in hotkeys that may conflict with these.
 * For example, `cmd+p` is often used to print the current page.
 * Make sure to choose hotkeys that do not conflict with common browser hotkeys,
 * and user's expected behavior.
 * :::
 *
 * :::important
 * In a menu, some keys are reserved for built-in navigation and
 * activation (e.g. `enter`, `escape`, `space`, `tab`, and arrow keys).
 * These will be ignored as hotkeys, unless combined with other modifiers.
 * E.g. `enter` will be ignored, but `alt+enter` will work.
 * :::
 */
@Component({
    tag: 'limel-example-menu-hotkeys',
    shadow: true,
})
export class MenuHotkeysExample {
    @State()
    private lastSelectedItem: string;

    private items: Array<ListSeparator | MenuItem> = [
        { text: 'Edit message', commandText: 'E', hotkey: 'e' },
        { text: 'Mark unread', commandText: 'U', hotkey: 'u' },
        {
            text: 'Remind me',
            items: [
                { text: 'Later today' },
                { text: 'Tomorrow' },
                { text: 'Next week' },
            ],
        },
        { separator: true },
        { text: 'Copy link', commandText: 'L', hotkey: 'l' },
        { text: 'Copy message', commandText: '⌘C', hotkey: 'meta+c' },
        { separator: true },
        {
            text: 'Organize',
            commandText: 'O',
            hotkey: 'o',
            items: [
                { text: 'Move to', commandText: 'M', hotkey: 'm' },
                { text: 'Label as', commandText: '⇧L', hotkey: 'shift+l' },
                { text: 'Mute', commandText: '⌥M', hotkey: 'alt+m' },
            ],
        },
        {
            text: 'Connect to apps',
            items: [{ text: 'Trello' }, { text: 'Asana' }],
        },
        { separator: true },
        { text: 'Delete message…', commandText: 'delete', hotkey: 'delete' },
    ];

    public render() {
        return [
            <limel-menu items={this.items} onSelect={this.handleSelect}>
                <limel-icon-button
                    label="Menu"
                    icon="menu_2"
                    elevated={true}
                    slot="trigger"
                />
            </limel-menu>,
            <limel-example-value
                label="Last selected item"
                value={this.lastSelectedItem}
            />,
        ];
    }

    private handleSelect = (event: LimelMenuCustomEvent<MenuItem>) => {
        this.lastSelectedItem = event.detail.text;
    };
}
